.notification {
    min-height: 60px; max-width: 250px; border-radius: 4px; position: fixed; top: 80px; right: 50px; color: #fff; display: flex; align-items: center; justify-content: center; padding: 0 15px; text-align: center; z-index: 1060;
}

.notification.success { 
    background-color: #4d9f4d;
}
.notification.error {
    background-color: #ed193b;
}
.notification .close {
    position: absolute;
    right: 5px;
    top: 3px;
}
.notification.show {display: flex;}
.notification.hide {display: none;}