@font-face {
  font-family: 'SourceSansPro';
  src: url('../fonts/SourceSansPro-Regular.ttf') format('truetype'),
    url('../fonts/sourcesanspro-regular-webfont.woff') format('woff'),
    url('../fonts/sourcesanspro-regular-webfont.woff2') format('woff2');
  font-weight: normal;
}

@font-face {
  font-family: 'SourceSansProSemiBold';
  src: url('../fonts/SourceSansPro-SemiBold.ttf') format('truetype'),
    url('../fonts/sourcesanspro-semibold-webfont.woff') format('woff'),
    url('../fonts/sourcesanspro-semibold-webfont.woff2') format('woff2');
  font-weight: normal;
}

iframe {
  pointer-events: none;
}
body {
  font-family: 'SourceSansPro', Arial, Helvetica, sans-serif;
  height: 100%;
  margin: 0;
}
.flex {
  display: flex;
}
.red-text {
  color: red;
}
.text-semi-bold {
  font-family: 'SourceSansProSemiBold';
}
.global-spinner {
  /* height: 100vh; */
  position: fixed;
  /* align-items: center; */
  /* flex-direction: column; */
  margin: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.hidesection {
  opacity: 0.5;
  pointer-events: none;
}
.rowHeading {
  font-size: 20px;
  font-weight: 700;
}
.feature-table {
  max-height: 280px;
  overflow: scroll;
}
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.text-bold {
  font-weight: bold;
}

.cursor-pointer {
  cursor: pointer;
}

.text-button {
  border: none;
  background: none;
}

.f-24 {
  font-size: 24px;
}

.actions {
  opacity: 0.6;
}

.actions i {
  margin-right: 12px;
}
.f28 {
  font-size: 28px;
}
.pointer {
  cursor: pointer;
}
.sm_label {
  font-size: 14px;
  color: gray;
}
.mr8 {
  margin-right: 8px;
}
.no_td {
  text-decoration: none;
}

.text-red {
  color: #dc3545;
}

::-webkit-scrollbar {
  height: 4px;
  width: 4px;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-thumb {
  background-color: #a7aebb;
  border-radius: 10px;
  z-index: -1;
  /* outline: 1px solid slategrey; */
}

.f-20 {
  font-size: 20px;
}

.spinner {
  /* margin: auto auto; */
  margin: 15% auto;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* background: #e2e2e2; */
}

.link-text {
  text-decoration: none;
}

.link-text:hover {
  color: #dc3545;
}
.no_content {
  text-align: center;
  text-transform: capitalize;
  font-size: 20px;
  background: #efefef;
  padding: 10px;
}

.cursor-none {
  cursor: none;
}
