.custom-modal {
  width: 100%;
  max-width: fit-content;
}

.custom-modal hr,
.confirm-modal hr {
  margin: 0;
}
.modal-dialog {
  max-width: 680px;
}
.modal-body {
  padding-bottom: 20px;
}

.modal-container {
  overflow-y: auto;
}

.w-78 {
  width: 78%;
}
#add-suc {
  display: 'block';
  z-index: 1041;
}
@media screen and (min-width: 768px) {
  .custom-modal {
    width: auto;
    min-width: 90%;
  }
}

@media screen and (min-width: 992px) {
  .custom-modal {
    min-width: 660px;
  }
}

.modal-open {
  padding-right: 0px !important;
}
